
angularAdmin.controller('GalleryController', function($scope, Picture)
{

    $scope.data = {};

    $scope.loading = true;


    /* Initial Data */

    $scope.init = function()
    {
        $scope.data = {
            pictures: []
        };

        $('#pictures').val('');
    };

    $scope.init();


    /* Get Pictures */

    $scope.getPictures = function()
    {
        $scope.loading = true;

        Picture.get().then(function(response)
        {
            $scope.loading = false;
            $scope.pictures = response.data;
        });
    };

    $scope.getPictures();


    /* Show Modal */

    $scope.showModal = function()
    {
        $scope.init();

        $scope.frmPicture.$setPristine();
        $scope.frmPicture.$setUntouched();

        $('#modal-photo').modal('show');
    };


    /* Submit */

    $scope.submit = function($event)
    {
        $($event.currentTarget).prepend('<i class="fa fa-spinner fa-spin"></i>');
        $($event.currentTarget).prop('disabled', true);

        /* Upload photos */
        $scope.data.pictures = $('#pictures')[0].files;

        Picture.store($scope.data).then(function(response)
        {
            if(response.data.error == 0)
            {
                $scope.getPictures();

                $('#modal-photo').modal('hide');

                $($event.currentTarget).find('i').remove();
                $($event.currentTarget).prop('disabled', false);
            }
        });
    };


    /* Delete */

    $scope.delete = function(id)
    {
        var dialog = new jBox('Confirm', {
            'content': 'Voules-vous vraiment supprimer cette Photo ?',
            confirmButton: 'Oui',
            cancelButton: 'Non',
            confirm: function()
            {
                Picture.delete(id).then(function(response)
                {
                    console.log(response.data);
                    $scope.getPictures();
                },
                function (response)
                {
                    console.log(response.data);
                });
            }
        });

        dialog.open();
    };

});